.token-page {
	display: flex;
	width: 100%;
	height: 100%;
	align-items: center;
	justify-content: center;
	background: #bbb;
}

.token-page input {
	border: solid 1px #aaa;
	padding: 5px 10px;
	outline: none;
	font-size: 20px;
	width: 400px;
	border-radius: 5px;
}

.token-page button {
	margin-left: 10px;
	border: none;
	padding: 7px 15px;
	font-size: 18px;
	background: #2f2f2f;
	color: #fff;
	border-radius: 5px;
	font-weight: bold;
	letter-spacing: 1px;
}
