.dashboard {
	background: #bbb;
}

.negate-all {
	position: fixed;
	background: #f00;
	color: #fff;
	border: none;
	padding: 5px 10px;
	font-size: 18px;
	font-weight: bold;
	margin: 10px;
	z-index: 2;
	cursor: pointer;
}

.counter {
	position: fixed;
	right: 10px;
	top: 10px;
	background: #2b3b82;
	color: #fff;
	font-size: 18px;
	padding: 7px 10px;
	letter-spacing: 1px;
	border-radius: 5px;
	z-index: 2;
}

.list {
	display: flex;
	width: 100%;
	min-height: 100%;
	flex-wrap: wrap;
	padding-top: 50px;
}

.list .item {
	width: 25%;
	box-sizing: border-box;
	padding: 10px;
}

.list .item .content {
	display: flex;
	flex-direction: column;
	border: solid 1px #cdcdcd;
	background: #efefef;
	width: 100%;
	height: 100%;
	border-radius: 10px;
	box-shadow: 0 0 0 5px rgba(0, 0, 0, .1);
}

.list .item .content .img-block {
	position: relative;
	display: inline-block;
	width: 100%;
}

.list .item .content .img-block img {
	display: inline-block;
	width: 100%;
	border-top-left-radius: 10px;
	border-top-right-radius: 10px;
}

.list .item .content .img-block:hover .thumbs {
	opacity: 1;
}

.list .item .content .img-block .thumbs {
	opacity: 0;
	position: absolute;
	width: 100%;
	bottom: 0;
	background: rgba(0, 0, 0, .4);
	padding: 5px;
	box-sizing: border-box;
	transition: opacity .25s ease-out;
}

.list .item .content .img-block .thumbs img {
	display: inline-block;
	width: calc(100% / 5 - 10px);
	margin: 5px;
	cursor: pointer;
}

.list .item .content .thumbs img.active {
	outline: solid 4px #ff5e00;
}

.list .item .content .info {
	box-sizing: border-box;
	padding: 10px;
	height: 100%;
}

.list .item .content .info h1 {
	margin: 0;
}

.list .item .content .actions {
	display: inline-block;
	width: 100%;
}

.list .item .content .actions button {
	display: inline-block;
	border: none;
	width: 50%;
	font-size: 18px;
	padding: 10px;
	cursor: pointer;
	outline: none;
}

button.disabled {
	width: 100% !important;
	border-bottom-left-radius: 10px;
	border-bottom-right-radius: 10px;
}

button.hidden {
	display: none !important;
}

button.like {
	background: #22eabc;
	border-bottom-right-radius: 10px;
}

button.dislike {
	background: #fe4a69;
	border-bottom-left-radius: 10px;
}

